/**
    
    @param {String} phone 
    @return {Boolean} 
*/
const validPhone = phone => {
	return /^(1)[0-9]{10}$/.test(phone)
}

/**
    
    @param {String} code 
    @return {Boolean} 
*/
const validCode = code => {
	return /^\d{6}$/.test(code)
}


/**
    
    @param {String} email 
    @return {Boolean} 
*/
const validEmail = email => {
	if (new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$").test(email)) {
		return true
	}
	return false
}

/**
    
    @param {String} card 
    @return {Boolean} 
*/
const validCard = card => {
	if (/(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}$)/.test(card)) {
		return true
	}
	return false
}

/**
    
    @param {String} name 
    @return {Boolean} 
*/
const validName = (name) => {
	if (name.indexOf('') > -1 || name.indexOf('') > -1) {
		return /^[\u4e00-\u9fa5]+[][\u4e00-\u9fa5]{1,18}$/.test(name)
	} else {
		return /^[\u4e00-\u9fa5]{2,20}$/.test(name) || /^[a-zA-Z0-9]{2,31}$/.test(name)
	}
}

/**
    
    @param {String} val 
    @return {Boolean} 
*/
const vailNumber = function(val) {
	return /^\d+$/.test(val)
}

/**
 * @description: 
 * @param {*} val
 * @return {*}
 */
const validSpecialKey=function(val){
	const regex = /[#|[\]`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/;

	return !regex.test(val);
}


export {
	validPhone,
	validCode,
	validEmail,
	validCard,
	validName,
	vailNumber,
	validSpecialKey
}


