/**
 * echarts
 */
export const getProdChartData = function (res) {
	res = [{ name: '', material: 200, output: 200, unit: 'kg' },
		{ name: '', material: 200, output: 200, unit: 'kg' }]
	const color = ['#4ECBB3', '#185EF0']
	return {
		tooltip: {
			trigger: 'item'
		},
		color: color,
		legend: {
			show: false
			// orient: 'horizontal',
			// //width: '300',
			// //padding: [60, 0],
			// left: 'right',
			// align: 'left',
			// textStyle: {
			// 	color: ['#4ECBB3', '#185EF0'],
			// 	padding: [0, 12],
			// 	lineHeight: 16
			// },
			// formatter: function (name) {
			// 	console.log(name)
			// 	for (let i = 0; i < res.length; i++) {
			// 		const item = res[i]
			// 		if (item.name === name) {
			// 			return `${name} ${item.value}kg/ \n  ${item.ringgit}`
			// 		}
			// 	}
			// }
		},
		tooltip: {
			position: 'right'
		},
		series: [
			{
				name: '',
				type: 'pie',
				center: ['30%', '50%'],
				radius: ['35%', '65%'],
				//avoidLabelOverlap: false,
				// label: {
				// 	show: true,
				// 	position: 'center',
				// 	formatter: '200kg',
				// 	color: "#629B88",
				// 	fontSize: 13,
				// 	fontWeight: "bold"
				// },
				label: {
					show: true,
					position: 'outside',
					padding:[0, -200,0,0],
					//position: 'right',
					formatter: function (params) {
						const prodItem = res[params.dataIndex]
						return `   {block${params.dataIndex}|} {a| ${params.name} }{b| ${params.percent}% }\n{c|${prodItem.material}${prodItem.unit} ${prodItem.output}${prodItem.unit}}`
						
					},
					fontSize: 13,
					color: '#333',
					
					rich: {
						block0: {
							width: 20,
							height: 8,
							backgroundColor: '#4ECBB3',
						},
						block1: {
							width: 20,
							height: 8,
							backgroundColor: '#185EF0',
						},
						a: {
							color: '#222222',
							fontSize: 12,
							lineHeight: 17,
							align: 'center',
							fontWeight: 400,
						},
						b: {
							color: '#222222',
							fontSize: 18,
							fontWeight: '500',
							lineHeight: 25
						},
						c: {
							color: '#666',
							fontSize: 12,
							lineHeight: 17,
							align: 'center',
							fontWeight: 400,
						}
					}
				},
				labelLine: {
					//center: ['0%', '0%']
					lineStyle: {
						dashOffset: 50
					}
				},
				labelLayout(params) {
					
					return {
						x: 175,
						y: params.labelRect.y+10,
						verticalAlign: 'middle',
						align: 'left'
					}
				},
				clockwise: false,
				emphasis: {
					disabled: true,
					label: {
						show: true,
						fontSize: 40,
						fontWeight: 'bold',
					}
				},

				data: [
					{ value: 750, name: '' },
					{ value: 250, name: '' }
				]
			}
		]
	}
}

