/**
 * 
 */
const userPhoneStorage = {
	/**
	 
	 @param { String } phone 
	 @return
	 */
	setUserPhone(phone) {
		localStorage.setItem('userPhone', phone)
	},

	/**
	 
	 @param
	 @return { String } 
	 */
	getUserPhone() {
		return localStorage.getItem('userPhone') || ""
	},

	/**
	 
	 @param
	 @return
	 */
	removeUserPhone() {
		localStorage.removeItem('userPhone')
	}
}




export {
	staffTabStorage,
	staffUserDatatorage,
	enterpriseTabStorage,
	enterpriseUserDataStorage,
	agriculturalTabStorage,
	userPhoneStorage,
	userDatatorage
}


